
import { reactive, toRefs, ref, Ref, onMounted, onActivated, onUpdated } from "vue"

export default {
  name: "SwiperCpm",
  setup(props: any, context: any) {
    const swiper = ref(null) as Ref
    const state = reactive({
      page: 0,
    })

    const handlePrev = () => {
      swiper.value.prev()
    }
    const handleNext = () => {
      swiper.value.next()
    }

    return {
      ...toRefs(state),
      handlePrev,
      handleNext,
      swiper,
    }
  },
}
